import {ApiConfig, ApiSourceConfig} from "./types";

export function generateCollectionName(connectorConfig: ApiSourceConfig, sinkConfig: ApiConfig): string {
  return `${connectorConfig.datasourceIdentifier}_${sinkConfig.name}`;
}

export function generateKafkaTopic(connectorConfig: ApiSourceConfig): string {
  return `${connectorConfig.tenantIdentifier}_SOURCE_${connectorConfig.datasourceIdentifier}`;
}

export function generateOffsetIdentifier(apiConfig: ApiConfig): string {
  return `${apiConfig.offsetFilePrefix ?? 'offset'}_${apiConfig.name}`;
}
